





























































import {
  Component, Prop, Vue, Emit,
} from 'vue-property-decorator';
import FormItem from '@/components/FormItem.vue';
import CheckBox from '@/components/CheckBox.vue';
import Button from '@/components/Button.vue';
import Errors from '@/components/Errors.vue';
import { jobType } from '@/data/data';

@Component({
  components: {
    FormItem,
    CheckBox,
    Button,
    Errors,
  },
})
export default class Modal extends Vue {
  @Prop() private date!: string

  @Prop() private startTime!: string

  @Prop() private endTime!: string

  jobTypes = jobType;

  jobs: string[] = [];

  errors: string[] = [];

  isAnimating = false;

  isSuccess = false;

  @Emit('resetShowModal')
  closeModal() {
    const modal = this.$refs.modal as HTMLElement;
    modal.classList.remove('is-shown');
    this.isAnimating = false;
    this.isSuccess = false;
  }

  addJob(job: string) {
    if (typeof job !== 'string') {
      this.jobs = this.jobs.filter((j) => j !== job.target.value);
      return;
    }
    this.jobs = [...this.jobs, job];
    this.errors = [];
  }

  register() {
    if (this.jobs.length < 1) {
      this.errors.push('選択してください。');
      return;
    }
    this.isAnimating = true;
    window.setTimeout(this.done, 3000);
    window.setTimeout(this.closeModal, 4500);
  }

  done() {
    this.isSuccess = true;
  }
}
